import {Component, OnInit} from '@angular/core';

@Component({
    moduleId: module.id,
    selector: 'admin-hirek-header',
    template: `
<div class="container-fluid ">
        <div class="row">
        <header >
            <a href="#/admin/hirek"><div class="col-md-6 headCol">
                <div class="head-labels"  [ngStyle]="col_1style"> Hirek listája</div>
            </div></a>
             <a href="#/admin/hirek/new"><div class="col-md-6 headCol">
                <div class="head-labels" [ngStyle]="col_2style">Új Hir felvétele</div>
            </div></a>
                </header>
    </div>
  <router-outlet></router-outlet>
</div>
    `,
    styleUrls: ["admin.css"],

})

export class AdminHirekHeaderComponent implements OnInit {
    col_1style:any;
    col_2style:any;
    ngOnInit() {
        var links = window.location.href.split("/");
        let url = links[links.length - 1];
        switch (url) {
            case 'Hirekok':
                this.col_1style ={'background-color':'#000066'};
                break;
            case 'new':
                this.col_2style ={'background-color':'#000066'};
            break;

            default:
                this.col_1style ={'background-color':'#000066'};
            break;
        }


    }


}
